
import './App.css';
import Navbar from './Components/Navbar'
import LoadingBar from 'react-top-loading-bar';
import React, { Component } from 'react'
import News from './Components/News';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


export default class App extends Component {

  state={
    progress:0
  }
  setProgress = (progress)=>{
    this.setState({progress: progress})
  }
  
  apikey = "f6492e900f2944848605db05cf98b306"

  render() {
    return (
      <div>
      <Router>
      <LoadingBar
        color='white'
        progress={this.state.progress}
        
      />
        <Navbar/>
        <Switch>
          <Route exact path="/"><News News setProgress={this.setProgress} apikey={this.apikey}   key="general"  pageSize={5} country = "in" category ="general"/></Route>
          <Route exact path="/general"><News News setProgress={this.setProgress} apikey={this.apikey} key="general"  pageSize={5} country = "in" category ="general"/></Route>
          <Route exact path="/business"><News News setProgress={this.setProgress} apikey={this.apikey} key="business"  pageSize={5} country = "in" category ="business"/></Route>
          <Route exact path="/entertainment"><News News setProgress={this.setProgress} apikey={this.apikey} key="entertainment"  pageSize={5} country = "in" category ="entertainment"/></Route>
          <Route exact path="/general"><News News setProgress={this.setProgress} apikey={this.apikey}  key="general" pageSize={5} country = "in" category ="general"/></Route>
          <Route exact path="/health"><News News setProgress={this.setProgress} apikey={this.apikey} key="health"  pageSize={5} country = "in" category ="health"/></Route>
          <Route exact path="/science"><News News setProgress={this.setProgress} apikey={this.apikey} key="science"  pageSize={5} country = "in" category ="science"/></Route> 
          <Route exact path="/sports"><News News setProgress={this.setProgress} apikey={this.apikey} key="sports"  pageSize={5} country = "in" category ="sports"/></Route>    
          <Route exact path="/technology"><News News setProgress={this.setProgress} apikey={this.apikey} key="technology"  pageSize={5} country = "in" category ="technology"/></Route>
        </Switch>   
       </Router>
      </div>
    )
  }
} 